.picture {
  position: relative !important;
}

.gblock {
  background-color: #43d9ad;
  width: 400px;
  height: 400px;
  border-radius: 50px;
  position: absolute;
  top: 0;
  left: 0;
  filter: blur(170px);
}

.bblock {
  background-color: #4d5bce;
  width: 400px;
  height: 400px;
  border-radius: 50px;
  position: absolute;
  bottom: 0;
  right: 0;
  filter: blur(170px);
}

.github {
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
}

@media (max-width: 767px) {
  .img {
    width: 300px;
    height: 300px;
    left: 50%;
    transform: translateY(-380px) translateX(-50%);
    margin-bottom: -150px;
  }
  .bblock,
  .gblock {
    width: 200px;
    height: 200px;
    transform: translateY(-400px);
    filter: blur(130px);
  }
  .info {
    margin-top: 70px;
  }
  .comment {
    margin-top: 300px !important;
  }
  .name {
    font-size: 30px !important;
  }
  .developer {
    font-size: 20px !important;
  }
  .github {
    font-size: 14px;
  }
}
