.sidebar {
  height: 100%;
  width: 15.3%;
  border-right: 1px solid #1e2d3d;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1400px) {
  .sidebar {
    width: 20.5%;
  }
}
@media (max-width: 1080px) {
  .sidebar {
    width: 31%;
  }
}
@media (max-width: 767px) {
  .sidebar {
    width: 100%;
  }
}

.sidebar-title {
  font-size: 16px;
  border-bottom: 1px solid #1e2d3d;
  line-height: 45px;
  padding-left: 15px;
  cursor: pointer;
  user-select: none;
}
.sidebar-title .text {
  padding-left: 10px;
}

.personal-list {
  border-bottom: 1px solid #1e2d3d;
  padding-left: 40px;
}

.contact-list {
  border-bottom: 1px solid #1e2d3d;
  padding-left: 40px;
}

.file {
  padding: 5px 5px;
}

.file:hover {
  background-color: #7e7e7e;
}

.file .text {
  padding-left: 10px;
  cursor: pointer;
}

.skills h5 {
  margin-bottom: 10px;
}

.skill-card {
  border: 1px solid #607b96;
  width: 300px;
  padding-bottom: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
  padding-top: 5px;
}

.skill-card:hover {
  background-color: #607b96;
  cursor: pointer;
}

.skill-image {
  padding: 0 !important;
  margin: 0 !important;
  position: relative;
}

.skill-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.progress-bar {
  background-color: #fea55f !important;
}
@media (max-width: 767px) {
  .skill-name {
    margin-left: -15px;
    margin-right: 10px;
  }
}

.form-label {
  color: #607b96;
  font-size: 16px;
}

.form-input {
  background-color: #011221;
  border: 1px solid #1e2d3d;
  border-radius: 7px;
  padding: 5px 10px;
  margin: 0;
  color: #607b96;
  width: 250px;
}

.form-btn {
  background-color: #1c2b3a;
  color: #fff;
  padding: 10px;
  width: 200px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  border: 1px solid #1c2b3a;
  transition: 0.3s;
}
.form-btn:hover {
  background-color: #011627;
}
