.filter-list {
  border-bottom: 1px solid #1e2d3d;
  padding-left: 35px;
}

.card {
  background-color: #011221 !important;
  margin: 10px;
  height: 400px !important;
}

.card-title {
  color: #5565e8 !important;
}

.card-body {
  color: #607b96 !important;
  height: 105px;
}

.card-btn {
  background-color: #1c2b3a !important;
  color: #fff;
  border: none !important;
  margin: 0 5px;
  transition: 0.3s !important;
}

.card-btn:hover {
  background-color: #fea55f !important;
}

.projects-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 767px) {
  .projects-container {
    justify-content: center;
  }
}
