.header {
  width: 100%;
  border-bottom: 1px solid #1e2d3d;
  font-size: 16px;
}

.title {
  width: 15%;
  color: #607b96;
  padding: 15px 15px;
}

.nav {
  width: 100%;
  position: relative;
}
.nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.nav ul li {
  padding: 0 15px;
  font-size: 16px;
  color: #607b96;
  width: 120px;
  text-align: center;
  border-left: 1px solid #1e2d3d;
  border-right: 1px solid #1e2d3d;
  height: 55px;
  line-height: 55px;
  cursor: pointer;
  position: relative;
}
.nav ul li:not(.active)::before {
  position: absolute;
  content: "";
  width: 0;
  height: 3px;
  background-color: #fea55f;
  bottom: 0;
  left: 0;
  transition: 0.3s;
}
.nav ul li:hover:not(.active)::before {
  width: 100%;
}
.nav ul li.active {
  color: #fff;
  border-bottom: 3px solid #fea55f;
}
.nav ul li.contact {
  margin-left: auto;
  width: 150px;
}
.menu {
  display: none;
  font-size: 25px;
  color: #607b96;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
}
@media (max-width: 1400px) {
  .title {
    width: 20%;
  }
}
@media (max-width: 1080px) {
  .title {
    width: 30%;
  }
}
@media (max-width: 767px) {
  .nav ul {
    display: none;
  }
  .title {
    width: 100%;
  }
  .menu {
    display: block;
  }
}
